import React from 'react';
import PropTypes from 'prop-types';

import './Movie.css';

function Movie({ id, title, poster, summary, rating, runtime, genres, language }) {
    return (
        <div className="movie-card">
            <div
                className="movie-thumb"
                style={{ backgroundImage: 'url("' + poster + '")' }}
            >
                {!language ? '' : (<span className="movie-lang">{language}</span>)}
            </div>
            <div className="movie-data">
                <h2 className="movie-title">{title}</h2>
                <div className="movie-simple">
                    <p className="movie-runtime">{runtime} min</p>
                    <p className="movie-rating">{rating} / 10.0</p>
                </div>
                <ul className="movie-genres">
                    {genres.map(genre => {
                        const key = id + '-' + genre;
                        return (
                            <li key={key}>{genre}</li>
                        )
                    })}
                </ul>
                <p className="movie-summary">{summary}</p>
            </div>
        </div>
    )
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    rating: PropTypes.number,
    runtime: PropTypes.number,
    genres: PropTypes.arrayOf(PropTypes.string),
    language: PropTypes.string
};

export default Movie;